/*
 * Leaflet Layer Mixin
 *
 * Handles lifecycle transitions for layers that need to talk to leaflet.
 */

var LReactMapLayerMixin = {

  componentDidMount: function() {
    this.addLayerToMap(this.props);
  },

  componentWillReceiveProps: function(nextProps) {
    var anyChanged = Object.keys(nextProps).some(function(propKey) {
      return nextProps[propKey] !== this.props[propKey];
    }, this);
    if (anyChanged) {
      this.addLayerToMap(nextProps);
    }
  },

  componentWillUnmount: function() {
    this.props.getMap().removeLayer(this.layer);
  },
};


module.exports = LReactMapLayerMixin;
